import React from "react";
import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  Divider,
  Modal,
  Carousel
} from "antd";
import { Link } from "react-router";
import "./Login.less";
import axios from "axios";
import { preURL } from "../../axios/config";
import { hashHistory } from "react-router";
import { instanceOf } from "prop-types";
import { withCookies, Cookies } from "react-cookie";
import { connect } from "react-redux";
import { setUser } from "../../actions";
import { bindActionCreators } from "redux";
const FormItem = Form.Item;

class Login extends React.Component {
  static propTypes = {
    cookies: instanceOf(Cookies).isRequired
  };
  state = {
    user: "",
    userType: 1,
    vcodeSrc: "",
    vcodeText: "",
    vcodeVerificationText: ""
  };

  componentWillMount() {
    const { cookies } = this.props;

    if (cookies.get("user")) {
      hashHistory.push("/app/recite");
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        axios({
          method: "post",
          url: preURL + "/api/login",
          dataType: "json",
          data: {
            email: values.email,
            password: values.password
          },
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          }
        })
          .then(response => {
            console.log("login response:", response);
            if (response.data.code === "200") {
              const { cookies, setUser } = this.props;
              const user = {
                name: response.data.data.name,
                email: values.email,
                setting: response.data.data.setting
              };
              setUser(user);
              cookies.set("user", user, {
                path: "/",
                maxAge: 24 * 3600
              });
              hashHistory.push("/app/recite");
            } else if (response.data.code === "1036") {
              Modal.error({ title: "", content: "!" });
            } else if (response.data.code === "1035") {
              Modal.error({ title: "", content: "" });
            } else if (response.data.code === "1034") {
              Modal.error({ title: "", content: "" });
            } else {
              Modal.error({ title: "", content: "" });
            }
          })
          .catch(error => {
            console.log("login error:", error);
          });
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="login_outer_all_container">
        <div className="innerdiv">
          <div className="carousel">
            <Carousel autoplay>
              <div>
                <img
                  alt=""
                  style={{ height: "20rem" }}
                  src="https://static.baydn.com/web/images/intro-client-checkin.png"
                />
                <p></p>
                <p></p>
              </div>
              <div>
                <img
                  alt=""
                  style={{ height: "20rem" }}
                  src="https://static.baydn.com/web/images/intro-client-community.png"
                />
                <p></p>
                <p></p>
              </div>
              <div>
                <img
                  alt=""
                  style={{ height: "20rem" }}
                  src="https://static.baydn.com/web/images/intro-client-insurance.png"
                />
                <p></p>
                <p></p>
              </div>
              <div>
                <img
                  alt=""
                  style={{ height: "20rem" }}
                  src="https://static.baydn.com/web/images/intro-client-academy.png"
                />
                <p></p>
                <p></p>
              </div>
            </Carousel>
          </div>
          <div className="login_all_container">
            <Form
              key="student_form"
              onSubmit={this.handleSubmit}
              className="login-form"
            >
              <FormItem className="login_form_title">
                <h2></h2>
                <Divider />
              </FormItem>
              <FormItem>
                {getFieldDecorator("email", {
                  rules: [
                    {
                      required: true,
                      message: "!"
                    }
                  ]
                })(
                  <Input
                    size="large"
                    prefix={
                      <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=""
                  />
                )}
              </FormItem>
              <FormItem>
                {getFieldDecorator("password", {
                  rules: [
                    {
                      required: true,
                      message: "!"
                    }
                  ]
                })(
                  <Input
                    size="large"
                    prefix={
                      <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    type="password"
                    placeholder=""
                  />
                )}
              </FormItem>
              <FormItem>
                <div className="suff_container">
                  <div className="suff_item">
                    {getFieldDecorator("remember", {
                      valuePropName: "checked",
                      initialValue: true
                    })(<Checkbox></Checkbox>)}
                  </div>
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="login-form-button"
                  >
                    
                </Button>
                  <Link
                    onClick={() => {
                      hashHistory.push("/userservice/signup");
                    }}
                  >
                    
                </Link>
                </div>
              </FormItem>
            </Form>
          </div>
        </div>
      </div>
    );
  }
}

const mapstateToProps = state => {
  const { user } = state;
  return { user };
};

const mapDispatchToProps = dispatch => {
  return {
    setUser: bindActionCreators(setUser, dispatch)
  };
};

const WrappedNormalLoginForm = Form.create()(Login);
export default connect(
  mapstateToProps,
  mapDispatchToProps
)(withCookies(WrappedNormalLoginForm));



